
import Icon from 'vux/src/components/icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import ToolCard from './ToolCard.vue'
import ToolTask from './ToolTask.vue'
import Food from './Food.vue'
export default {
name:'Main',
directives: {
TransferDom
},
components: {
Icon,
TransferDom,
Popup,
Tab,
TabItem,
ToolCard,
ToolTask,
Food
},
data(){
return{
dynamicstate: false,
stageproperty:false,
pleasureground:false,
community:false,
food:false,
toolcard:true,
tooltask:false,
coupon:false
}
},
methods: {

showDynamic(){
this.dynamicstate = true;
},

showStage(){
this.stageproperty = true;
},

showGround(){
this.pleasureground = true;
},

showCommunity(){
this.community = true;
},

showFood(){
this.food = true;
},

hideFood(){
this.food = false;
},

showMyTool(){
this.toolcard = true;
this.tooltask = false;
},
showToolTask(){
this.toolcard = false;
this.tooltask = true;
},

showCoupon(){
this.coupon = true;
}
}
}



